import { Grid, Card, CardActionArea, CardMedia, CardContent, Backdrop, Typography } from "@mui/material";
import React, {useState} from 'react';

import "./ProjectGlance.css"

const ProjectGlance = (props) => {

  const[open, setOpen] = useState(false);

  const[backDropContent, setbackDropContent] = useState();

  const[hover, setHover] = useState(false);

  const closeHandler = () => {
      setOpen(false);
  };

  const backdropHandler = (event) => {
    setOpen(!open);
  };

  const hoverOnHandler = (index) => {
    setHover(true);
  }

  const hoverOffHandler = (event) => {
    setHover(false);
  }

  const hoverStyle  = {
    backgroundColor: 'rgb(0,0,0,0.4)',
    opacity: hover ? 1 : 0,
    transition: 'display 5s',
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    transition: 'opacity 0.5s'
  }

  return (
    <div>
      <Grid container spacing={10} justifyContent='center' p={3}>
        {props.projectGlance.map((item,index) => {
          return (
            <Grid item xs={12} sm={12} md={6}>
              <Card className = "project" onMouseEnter={() => hoverOnHandler(index)} onMouseLeave={hoverOffHandler} >
                  <CardActionArea onClick={backdropHandler}>
                  <div className = 'wallpaper'>
                    <CardMedia
                      component="img"
                      image= {item.img}
                      alt="example"
                    />
                  </div>
                  <div style={hoverStyle} id={index}>
                    <div className="overlay-contents">
                      <h6>{item.area}</h6>
                      <h4 className="name">{item.name}</h4>
                    </div>
                  </div>
                </CardActionArea>
              </Card>
            </Grid>
          )
        })}
      </Grid>
      <Backdrop open={open} onClick = {closeHandler}>
        
      </Backdrop>
    </div>
  )
}

export default ProjectGlance;